<template>
  <div class="h-100 p-0 m-0 d-flex">
    <SideBar></SideBar>
    <div class="content" style="width: calc(100% - 80px)">
      <div v-if="toggleAboutPage">
        <About></About>
      </div>
      <div v-else>
        <GameContent
            :gridArray="gridArray"
            :statusMessage="statusMessage"
        ></GameContent>
      </div>
    </div>
  </div>
</template>

<script>
import SideBar from '@/views/SideBar'
import GameContent from '@/views/GameContent'
import About from '@/views/About'
import { EventBus } from '@/plugins/EventBus'
import axios from 'axios'
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'Game',
  components: {
    About,
    GameContent,
    SideBar
  },
  data: () => ({
    toggleAboutPage: false,
    gridArray: [],
    statusMessage: ''
  }),
  computed: {
    ...mapGetters([
      'players',
      'game'
    ])
  },
  methods: {
    ...mapActions([
      'setGame'
    ])
  },
  created() {
    EventBus.$on('toggleAbout', () => {
      this.toggleAboutPage = !this.toggleAboutPage
    })
    EventBus.$on('homeScreen', () => {
      this.toggleAboutPage = false
    })
    axios.get('/json').then(({ data }) => {
      this.setGame(data)
    })
    this.gridArray = this.game.gridArray
    this.statusMessage = this.game.statusMessage
  }
}
</script>

